<template>
  <div class="pa-5 pb-0">
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: 'Дата' },
        {
          filterName: 'road',
          filterType: 'select',
          labelText: 'Дорога',
          isRemote: true,
          filterUrl: '../../server/road_filter.json'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Buttons :params="params" />
    <div class="text-info">
      План работы технических станций Восточный полигон на
      <span class="text-info-bold">{{ getDate }}</span>
    </div>
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :headersFlat="headersFlat"
      :url="url"
      :params="{ date: Math.floor(Date.now() / 1000), road: 1 }"
      ref="table"
      :tableType="tableType"
      :hasDetail="true"
      :hasInfo="true"
      @loadDetail="setDetail"
    />
    <DetailPanel
      :headers="detailHeaders"
      :headersFlat="detailHeaders"
      ref="detailPanel"
    />
  </div>
</template>

<script>
import Filters from '../components/Filters';
import Buttons from '../components/Buttons';
import Tabs from '../components/Tabs';
import DataTable from '../components/DataTable';
import DetailPanel from '../components/DetailPanel';

export default {
  name: 'PlanningTechStationTasks',
  components: { Buttons, Filters, Tabs, DataTable, DetailPanel },
  computed: {
    getDate() {
      let d =
        this.$store.state.filtersValues.date ||
        new Date().toISOString().substr(0, 10);
      const [year, month, day] = d.split('-');
      return `${day}.${month}.${year}`;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        road: this.$store.state.filtersValues.road
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      // this.$refs.detailPanel.updateDetail(this.detailUrl);
      this.$refs.detailPanel.updateDetail(
        '../../server/work_station_detail.json'
      ); //пока тестовые данные
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      url: '../../server/work_station.json',
      tableType: 'st_tasks',
      params: {
        buttons: {
          print: true
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 0,
      detailHeaders: [],
      headersFlat: [
        { text: 'Дорога', value: 'road' },
        {
          text: 'Станция',
          value: 'station_name'
        },
        { text: 'Направление', value: 'direction_name' },
        { text: 'до 6:00 ч', value: 'PRIB_vse_06_D' },
        { text: 'сутки', value: 'PRIB_vse_18_D' },
        { text: 'до 6:00 ч', value: 'RASF_vse_06_D' },
        { text: 'сутки', value: 'RASF_vse_18_D' },
        { text: 'до 6:00 ч', value: 'OTPR_vse_06_D' },
        { text: 'сутки', value: 'OTPR_vse_18_D' },
        { text: 'до 6:00 ч', value: 'OTPR_SF_vse_06_D' },
        { text: 'сутки', value: 'OTPR_SF_vse_18_D' }
      ],
      headers: [
        {
          text: 'Дорога',
          alignHeader: 'center',
          value: 'road',
          border: true
        },
        {
          text: 'Станция',
          alignHeader: 'center',
          value: 'station_name',
          border: true
        },
        {
          text: 'Направление',
          alignHeader: 'center',
          value: 'direction_name',
          border: true
        },
        {
          text: 'Прибытие',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'до 6:00 ч',
              value: 'PRIB_vse_06_D',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'сутки',
              value: 'PRIB_vse_18_D',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: 'В расформирование',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'до 6:00 ч',
              value: 'RASF_vse_06_D',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'сутки',
              value: 'RASF_vse_18_D',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: 'Отправление',
