          alignHeader: 'center',
          children: [
            {
              text: 'до 6:00 ч',
              value: 'OTPR_vse_06_D',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'сутки',
              value: 'OTPR_vse_18_D',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: 'Своего формирования',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'до 6:00 ч',
              value: 'OTPR_SF_vse_06_D',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'сутки',
              value: 'OTPR_SF_vse_18_D',
              alignHeader: 'center',
              border: true
            }
          ]
        }
      ]
    };
  }
};
</script>

